"use client"

import { useState, useEffect, useCallback } from "react"
import { Clock, ChefHat, Bell, X, RefreshCw, Volume2, VolumeX } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter } from "@/components/ui/dialog"
import { Textarea } from "@/components/ui/textarea"

interface KitchenOrder {
  id: string
  tableId: string
  items: Array<{
    menuItem: { name: string }
    quantity: number
    selectedModifiers?: Array<{ name: string }>
    notes?: string
  }>
  status: "pending" | "accepted" | "preparing" | "ready"
  createdAt: string
  notes?: string
}

const statusColumns = [
  { key: "pending", label: "Pending", icon: Clock, color: "bg-warning" },
  { key: "preparing", label: "Preparing", icon: ChefHat, color: "bg-primary" },
  { key: "ready", label: "Ready", icon: Bell, color: "bg-success" },
]

export default function KitchenDisplayPage() {
  const [orders, setOrders] = useState<KitchenOrder[]>([])
  const [isSoundEnabled, setIsSoundEnabled] = useState(true)
  const [rejectingOrder, setRejectingOrder] = useState<KitchenOrder | null>(null)
  const [rejectReason, setRejectReason] = useState("")
  const [currentTime, setCurrentTime] = useState(new Date())

  const fetchOrders = useCallback(() => {
    // Get orders from localStorage
    const storedOrders = JSON.parse(localStorage.getItem("orders") || "[]")
    // Filter to show only kitchen-relevant orders
    const kitchenOrders = storedOrders.filter(
      (order: KitchenOrder) =>
        order.status === "pending" ||
        order.status === "accepted" ||
        order.status === "preparing" ||
        order.status === "ready",
    )
    setOrders(kitchenOrders)
  }, [])

  useEffect(() => {
    fetchOrders()
    // Simulate real-time updates
    const interval = setInterval(fetchOrders, 5000)
    return () => clearInterval(interval)
  }, [fetchOrders])

  useEffect(() => {
    // Update time every second for elapsed time display
    const timeInterval = setInterval(() => setCurrentTime(new Date()), 1000)
    return () => clearInterval(timeInterval)
  }, [])

  const getElapsedTime = (createdAt: string) => {
    const created = new Date(createdAt)
    const diff = Math.floor((currentTime.getTime() - created.getTime()) / 1000)
    const minutes = Math.floor(diff / 60)
    const seconds = diff % 60
    return `${minutes}:${seconds.toString().padStart(2, "0")}`
  }

  const getTimeColor = (createdAt: string) => {
    const created = new Date(createdAt)
    const diff = Math.floor((currentTime.getTime() - created.getTime()) / 1000 / 60)
    if (diff >= 15) return "text-destructive"
    if (diff >= 10) return "text-warning"
    return "text-muted-foreground"
  }

  const updateOrderStatus = (orderId: string, newStatus: string) => {
    const storedOrders = JSON.parse(localStorage.getItem("orders") || "[]")
    const updatedOrders = storedOrders.map((order: KitchenOrder) =>
      order.id === orderId ? { ...order, status: newStatus } : order,
    )
    localStorage.setItem("orders", JSON.stringify(updatedOrders))
    fetchOrders()

    // Play sound for ready status
    if (newStatus === "ready" && isSoundEnabled) {
      // Would play notification sound in production
    }
  }

  const handleAcceptOrder = (orderId: string) => {
    updateOrderStatus(orderId, "preparing")
  }

  const handleReadyOrder = (orderId: string) => {
    updateOrderStatus(orderId, "ready")
  }

  const handleRejectOrder = () => {
    if (!rejectingOrder || !rejectReason) return

    // In production, this would notify the customer
    const storedOrders = JSON.parse(localStorage.getItem("orders") || "[]")
    const updatedOrders = storedOrders.filter((order: KitchenOrder) => order.id !== rejectingOrder.id)
    localStorage.setItem("orders", JSON.stringify(updatedOrders))

    setRejectingOrder(null)
    setRejectReason("")
    fetchOrders()
  }

  const getOrdersByStatus = (status: string) => {
    return orders.filter((order) =>
      status === "pending" ? order.status === "pending" || order.status === "accepted" : order.status === status,
    )
  }

  return (
    <div className="flex min-h-screen flex-col bg-background">
      {/* Header */}
      <header className="flex items-center justify-between border-b border-border bg-card px-4 py-3">
        <div className="flex items-center gap-3">
          <div className="flex h-10 w-10 items-center justify-center rounded-lg bg-primary">
            <ChefHat className="h-6 w-6 text-primary-foreground" />
          </div>
          <div>
            <h1 className="text-lg font-bold text-card-foreground">Kitchen Display</h1>
            <p className="text-sm text-muted-foreground">
              {currentTime.toLocaleTimeString("vi-VN", { hour: "2-digit", minute: "2-digit" })}
            </p>
          </div>
        </div>

        <div className="flex items-center gap-2">
          <Button variant="outline" size="icon" onClick={() => setIsSoundEnabled(!isSoundEnabled)}>
            {isSoundEnabled ? <Volume2 className="h-4 w-4" /> : <VolumeX className="h-4 w-4" />}
          </Button>
          <Button variant="outline" size="icon" onClick={fetchOrders}>
            <RefreshCw className="h-4 w-4" />
          </Button>
        </div>
      </header>

      {/* Kanban Board */}
      <main className="flex flex-1 gap-4 overflow-x-auto p-4">
        {statusColumns.map((column) => {
          const Icon = column.icon
          const columnOrders = getOrdersByStatus(column.key)

          return (
            <div key={column.key} className="flex w-80 shrink-0 flex-col rounded-lg border border-border bg-card">
              {/* Column Header */}
              <div className={`flex items-center gap-3 rounded-t-lg ${column.color} p-3`}>
                <Icon className="h-5 w-5 text-white" />
                <h2 className="font-semibold text-white">{column.label}</h2>
                <Badge className="ml-auto bg-white/20 text-white">{columnOrders.length}</Badge>
              </div>

              {/* Orders */}
              <div className="flex-1 space-y-3 overflow-y-auto p-3">
                {columnOrders.length === 0 ? (
                  <div className="py-8 text-center text-muted-foreground">
                    <p>No orders</p>
                  </div>
                ) : (
                  columnOrders.map((order) => (
                    <div key={order.id} className="rounded-lg border border-border bg-background p-3">
                      {/* Order Header */}
                      <div className="mb-3 flex items-center justify-between">
                        <div className="flex items-center gap-2">
                          <span className="font-mono text-sm font-bold text-card-foreground">
                            #{order.id.slice(-6)}
                          </span>
                          <Badge variant="outline">T{order.tableId}</Badge>
                        </div>
                        <div className={`flex items-center gap-1 text-sm font-mono ${getTimeColor(order.createdAt)}`}>
                          <Clock className="h-3 w-3" />
                          {getElapsedTime(order.createdAt)}
                        </div>
                      </div>

                      {/* Order Items */}
                      <div className="mb-3 space-y-2">
                        {order.items.map((item, index) => (
                          <div key={index} className="rounded bg-muted p-2">
                            <div className="flex justify-between">
                              <span className="font-medium text-card-foreground">
                                {item.quantity}x {item.menuItem.name}
                              </span>
                            </div>
                            {item.selectedModifiers && item.selectedModifiers.length > 0 && (
                              <p className="text-xs text-muted-foreground">
                                {item.selectedModifiers.map((m) => m.name).join(", ")}
                              </p>
                            )}
                            {item.notes && <p className="text-xs italic text-primary">{item.notes}</p>}
                          </div>
                        ))}
                      </div>

                      {/* Order Notes */}
                      {order.notes && (
                        <div className="mb-3 rounded bg-warning/10 p-2">
                          <p className="text-xs font-medium text-warning">Note: {order.notes}</p>
                        </div>
                      )}

                      {/* Actions */}
                      <div className="flex gap-2">
                        {column.key === "pending" && (
                          <>
                            <Button className="flex-1" size="sm" onClick={() => handleAcceptOrder(order.id)}>
                              Accept
                            </Button>
                            <Button
                              variant="outline"
                              size="sm"
                              className="text-destructive bg-transparent"
                              onClick={() => setRejectingOrder(order)}
                            >
                              <X className="h-4 w-4" />
                            </Button>
                          </>
                        )}
                        {column.key === "preparing" && (
                          <Button
                            className="flex-1 bg-success hover:bg-success/90"
                            size="sm"
                            onClick={() => handleReadyOrder(order.id)}
                          >
                            <Bell className="mr-1 h-4 w-4" />
                            Ready
                          </Button>
                        )}
                        {column.key === "ready" && (
                          <div className="flex-1 text-center">
                            <p className="text-sm text-success">Waiting for pickup</p>
                          </div>
                        )}
                      </div>
                    </div>
                  ))
                )}
              </div>
            </div>
          )
        })}
      </main>

      {/* Reject Dialog */}
      <Dialog open={!!rejectingOrder} onOpenChange={() => setRejectingOrder(null)}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>Reject Order #{rejectingOrder?.id.slice(-6)}</DialogTitle>
          </DialogHeader>
          <div className="py-4">
            <p className="mb-4 text-sm text-muted-foreground">
              Please provide a reason for rejecting this order. The customer will be notified.
            </p>
            <Textarea
              placeholder="e.g., Ingredient unavailable, Kitchen closed..."
              value={rejectReason}
              onChange={(e) => setRejectReason(e.target.value)}
              rows={3}
            />
          </div>
          <DialogFooter>
            <Button variant="outline" onClick={() => setRejectingOrder(null)}>
              Cancel
            </Button>
            <Button variant="destructive" onClick={handleRejectOrder} disabled={!rejectReason}>
              Reject Order
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </div>
  )
}
